* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root{
  height: 100%;
}
:root{
    
    --green: hsl(142, 100%, 40%);
    --red: hsl(0, 100%, 40%);
    --background: hsl(142, 2%, 95%);
    
    --text-light: hsl(142, 0%, 25%);
    --white-dark: hsl(142, 0%, 65%);
    
    --main: hsl(142, 100%, 30%);
    --shadow-light: rgb(27, 16, 16);
    --shadow-dark: hsl(142, 0%, 60%);

    --background-light: #f5f5f5;
    --violent: #10002b;
    --blue: #04a099;
    --white: hsl(142, 0%, 99%);

    --text-secondary: #d3d3d3;
    --shadow: #9c9c9c;
    --text-dark: hsl(142, 0%, 15%);
}

.card{
    padding: 10px;
    
    cursor: pointer;
    background-color: hsl(142, 0%, 99%);
    background-color: var(--white);
}

.card:hover{
    box-shadow: 0px 3px 20px hsl(142, 0%, 60%);
    box-shadow: 0px 3px 20px var(--shadow-dark);
}

.shadow{
    box-shadow: 0px 3px 15px #d3d3d3;
    box-shadow: 0px 3px 15px var(--text-secondary);
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.shadow:hover{
    box-shadow: 0px 3px 15px #9c9c9c;
    box-shadow: 0px 3px 15px var(--shadow);
}
.icon {
    font-size: 18px;
    margin-left: 5px;
    display: inline-flex;
    align-items: center;
    margin-right: 5px;
}

.icon_size_big {
    font-size: 24px;
}

.icon_size_huge{
    font-size: 48px;
    border: 2px solid var(--blue);
    border-radius: 50%;
    padding: 20px;
}

.icon_color_red {
    color: var(--red);
}

.icon_color_green {
    color: var(--green);
}

.icon_color_blue {
    color: blue;
}
.dashboard{
    height: 100%;
    display: flex;
    color: var(--text-dark);
}

.dashboard__content{
    width: 100%;
    background-color: var(--background-light);
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
}

.dashboard__name{
    height: 90px;
    flex-shrink: 0;
    font-size: 30px;
    padding-left: 30px;
    display: flex;
    align-items: center;
    color: var(--background-light);
    background-color: var(--blue);
}

.dashboard__logo{
    position: absolute;
    height: 90px;
    width: 180px;
    top: 25px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.menu {
    width: 220px;
    background-color: var(--background-light);
    color: var(--text-secondary);
    text-transform: uppercase;
    font-size: 14px;
    flex-shrink: 0;
}

.menu__header{
    background-color: var(--background-light);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90px;
    position: relative;
}

.menu__item{
    padding: 0 0 0 30px;
    height: 45px;
    display: flex;
    align-items: center;
    cursor: pointer;
    color: var(--white-dark);
    -webkit-transition-property: color, background-color;
    transition-property: color, background-color;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.menu__item_active_true, .menu__item:hover{
    color: var(--white);
    background-color: var(--blue);
}
.stat {
    height: 100%;
    width: 100%;
    background-color: var(--white);
    color: var(--text-dark);
    overflow: auto;
}

.counters {
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: var(--background-light);
    height: 135px;
}

.counter {
    width: 100%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.counter:hover .counter__val {
    color: var(--blue);
}

.counter__val {
    display: flex;
    align-items: center;
    font-size: 30px;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.counter__unit {
    color: var(--white-dark);
    font-size: 12px;
    text-transform: uppercase;
}

.counter .icon {
    margin: 0;
}

.profiles {
    display: flex;
}

.profile {
    padding: 10px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.profile__info {
    display: flex;
    margin-bottom: 5px;
    align-items: baseline;
}

.profile__prop {
    font-size: 12px;
    color: var(--white-dark);
    margin-right: 10px;
}

.profile__val{
    font-size: 14px;
    color: var(--violent);
}

.profile__image{
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: 2px solid var(--violent);
    margin-bottom: 20px;
    background-position: center center;
    background-size: contain;
}

.caption{
    color: var(--violent);
    font-size: 18px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.chart-container{
    height: 400px;
    overflow: auto;
}
.competitors{
    padding: 30px;
    overflow: auto;
    background-color: var(--background-light);
    color: var(--blue);
    height: 100%;
}

.competitors_empty_true{
    display: flex;
    align-items: center;
    justify-content: center;
}

.msg{
    font-size: 20px;
    color: var(--text-secondary);
}

.comp{
    padding: 0 15px 0 0;
    height: 90px;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    background-color: var(--white);
    position: relative;
}

.comp__logo-container{
    width: 90px;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.comp__logo{
    width: 60px;
    height: 60px;
    background-position: center center;
    background-size: contain;
    border-radius: 50%;
    background-image: url(https://d31kswug2i6wp2.cloudfront.net/fallback/company/medium_logo_default.png);
}

.comp:hover .comp__name{
    color: var(--violent);
}

.comp__info{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.comp__link{
    display: flex;
    align-items: center;
    position: absolute;
    right: 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    cursor: pointer;
    color: var(--white-dark);
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.comp__link:hover{
    color: var(--violent);
}

.comp__name{
    font-size: 20px;
    margin-bottom: 5px;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.comp__business{
    font-size: 16px;
    color: var(--white-dark);
}

.comp__address{
    font-size: 14px;
    color: var(--white-dark);
}
.advices {
    display: flex;
    height: 100%;
    justify-content: space-between;
    background-color: var(--background-light);
    color: var(--blue);
    overflow: auto;
}

.tip {
    width: 100%;
    text-align: center;
    padding: 10px;
}

.tip > .icon{
    margin-bottom: 30px;
}

.text {
    text-align: left;
    text-align: initial;
    font-size: 14px;
    padding: 10px;
    background-color: var(--white);
    margin-bottom: 20px;
    color: var(--violent);
    box-shadow: 0 3px 15px var(--text-secondary);
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.text:hover{
    box-shadow: 0 3px 15px var(--shadow);
}

.text__content{
    margin-bottom: 10px;
}

.text__button{
    cursor: pointer;
    color: var(--white-dark);
    font-size: 12px;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.text__button:hover{
    color: var(--violent);
}

.tip__ok{
    display: flex;
    align-items: center;
    color: var(--white-dark);
    justify-content: center;
}
.map {
    display: flex;
    height: 100%;
}

.map__filter {
    width: 200px;
    flex-shrink: 0;
    height: 100%;
}

.map__container {
    height: 100%;
    width: 100%;
}

.filter {
    color: var(--violent);
}

.filter-item {
    margin-bottom: 10px;
    padding: 0 10px;
}

.filter-item__input {
    width: 100%;
    margin: auto;
    -webkit-appearance: none;
}

.filter-item__name {
    font-size: 14px;
    color: var(--white-dark);
    margin-right: 10px;
}

.filter-item__row {
    display: flex;
}

.filter-item__button {
    margin: auto;
    background-color: var(--text-secondary);
    color: var(--white);
    width: 70%;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    -webkit-transition-property: background-color;
    transition-property: background-color;
    -webkit-transition-duration: 250ms;
            transition-duration: 250ms;
}

.filter-item__button:hover {
    background-color: var(--blue);
}

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 2px solid var(--blue);
    height: 15px;
    width: 15px;
    border-radius: 50%;
    background: var(--blue);
    cursor: pointer;
    margin-top: -5px; /* Вам нужно указать значение для поля в Chrome, но в Firefox и IE это происходит автоматически */
  }

  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 4px;
    cursor: pointer;
    background: var(--text-secondary);
    border-radius: 5px;
  }

  input[type=range]:focus {
    outline: none;
  }
